<template>
  <div>
    <div class="tips">
      <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="60%"
        :before-close="handleClose"
      >
        <span class="dialog_text">!</span>
          <br />
        <span class="dialog_text">(<a href="#"></a>)</span><br/>
        <br/>
        <span class="dialog_text"></span><br/><br/><br/>
        <span class="dialog_text"><a href="#"></a></span>
        <ul class="dialog_text">
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
        <span slot="footer" class="dialog-footer">
          <el-button id="tips_btn" @click="dialogVisible = false"></el-button>
        </span>
      </el-dialog>
    </div>
    <div class="Register_top">
      <img src="static\icon-03.png" alt="" />
      <div class="Register_top_text"></div>
      <div class="Register_top_text2">
        <span></span>
        <span
          ><router-link to="/LoginPage" class="please_Register"
            ></router-link
          ></span
        >
      </div>
    </div>
    <div class="Register_Mid">
      <regos_block></regos_block>
    </div>
    <div class="Register_bottom">
      <end_Info></end_Info>
    </div>
  </div>
</template>

<script>
import regos_block from "./Register/regis_block";
import end_Info from "./end_Info";
export default {
  props: [],
  data() {
    return {
      dialogVisible:true,
    };
  },
  methods: {
    handleClose() {
      this.$router.push("/Home");
    },
        init () {
      this.axios.get("/static/goodlist.json").then((res)=>{
        this.$store.commit("initData",res.data.goods);
      })
    }
  },
    created() {
    this.init();
  },
  components: {
    regos_block,
    end_Info,
  },
};
</script>

<style scope>
* {
  margin: 0px;
}
.Register_top {
  width: 1920px;
  height: 100px;
  /* background-color: coral; */
  border-bottom: 1px solid rgba(153, 153, 153, 0.3);
}
.Register_top img {
  width: 200px;
  margin: auto;
  position: relative;
  top: 5px;
  left: 450px;
  /* border:1px solid darkmagenta; */
}
.Register_view {
  width: 1920px;
  height: 680px;
  /* background-color: cyan; */
}
.Register_top_text {
  display: inline-block;
  color: #333333;
  font-size: 24px;
  position: relative;
  left: 480px;
  top: 8px;
  line-height: 80px;
  vertical-align: top;
}
.Register_top_text2 {
  display: inline-block;
  height: 30px;
  width: 140px;
  /* background-color: darkorchid; */
  position: relative;
  left: 980px;
  vertical-align: bottom;
  font-size: 16px;
  line-height: 30px;
  color: #777777;
}
.please_Register {
  color: red;
}
.please_Register:hover {
  color: red;
}
/* ---------------------------------------------------------------- */
.Register_Mid {
  display: inline-block;
  width: 1920px;
  height: 420px;
  text-align: center;
  /* background-color: tomato; */
}
/* ---------------------------------------------------------------- */
.Register_bottom {
  /* background-color: turquoise; */
  display: inline-block;
  width: 1920px;
  height: 100px;
  text-align: center;
  vertical-align: top;
  padding: 250px;
}
#tips_btn{
  width: 200px;
  background-color: #E1251B;
  color: #ffffff;
  position: relative;
  left: -500px;
}
.tips a{
  color: #E1251B;
}
.dialog_text{
  font-size: 16px;
  font-weight: 600;
}
</style>



